/* Header */

.header {
    padding: 40px 0;
    min-height: 695px;

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: black;
    color: #fff;

	border-bottom: green 2px solid !important;
}

.header__wrapper {
    padding: 0 15px;
	max-width: 660px;
}

.header__title {
    margin-bottom: 20px;
    font-size: 40px;
    font-weight: 700;
    line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
    color: green !important;
    font-size: 60px;
	font-weight: 700;
}

.header__text {
    margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
}